:root {
  --pink: #ff1a8c;
  --gray-soft: #555;
}
.sectionWrapper {
}
.sectionInner {
  max-width: 1100px;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  align-items: center;
}
.sectionTitle {
  font-family: var(--font-playfair), serif;
  color: var(--pink);
  font-size: 1.6rem;
  margin: 0 0 6px 0;
  text-align: center;
}
.testimonialGrid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  width: 100%;
  justify-items: center;
}
.testimonialCard {
  background: linear-gradient(180deg, #fff, rgba(255, 255, 255, 0.98));
  border-radius: 16px;
  padding: 18px;
  border: 2px solid rgba(255, 46, 122, 0.12);
  box-shadow: 0 20px 54px rgba(0, 0, 0, 0.06);
  text-align: center;
  max-width: 260px;
}
.avatar {
  border-radius: 999px;
  margin-bottom: 10px;
  width: 72px;
  height: 72px;
  border: 4px solid var(--pink-light);
  box-shadow: 0 8px 28px rgba(0, 0, 0, 0.08);
}
.quote {
  color: var(--gray-soft);
  margin: 0 0 8px 0;
  font-style: italic;
}
.person {
  color: var(--pink);
  font-weight: 800;
  margin: 0;
}
@media (min-width: 768px) {
  .testimonialGrid {
    grid-template-columns: repeat(4, 1fr);
  }
}
